"use client";

import { useState } from "react";
import { MainLayout } from "@/components/layout/main-layout";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { mockBeneficiaries } from "@/lib/mock-data";

export default function BeneficiariesPage() {
  const [searchTerm, setSearchTerm] = useState("");

  const filteredBeneficiaries = mockBeneficiaries.filter(beneficiary =>
    beneficiary.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    beneficiary.bankName.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const favoriteBeneficiaries = filteredBeneficiaries.filter(b => b.isFavorite);

  return (
    <MainLayout>
      <div className="space-y-6">
        <div className="flex justify-between items-center">
          <h1 className="text-3xl font-bold">Beneficiaries</h1>
          <Button>Add Beneficiary</Button>
        </div>

        {/* Search */}
        <Card>
          <CardContent className="pt-6">
            <Input
              placeholder="Search beneficiaries..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </CardContent>
        </Card>

        {/* Favorite Beneficiaries */}
        {favoriteBeneficiaries.length > 0 && (
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <span className="text-lg">‚≠ê</span>
                Favorite Beneficiaries
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid gap-4 md:grid-cols-2">
                {favoriteBeneficiaries.map((beneficiary) => (
                  <div key={beneficiary.id} className="flex items-center justify-between p-4 border rounded-lg">
                    <div className="flex items-center gap-3">
                      <Avatar className="h-12 w-12">
                        <AvatarImage src={beneficiary.avatar} alt={beneficiary.name} />
                        <AvatarFallback>{beneficiary.name[0]}</AvatarFallback>
                      </Avatar>
                      <div>
                        <p className="font-medium">{beneficiary.name}</p>
                        <p className="text-sm text-muted-foreground">{beneficiary.bankName}</p>
                        <p className="text-xs text-muted-foreground">{beneficiary.accountNumber}</p>
                      </div>
                    </div>
                    <Button size="sm">Send</Button>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        )}

        {/* All Beneficiaries */}
        <Card>
          <CardHeader>
            <CardTitle>All Beneficiaries ({filteredBeneficiaries.length})</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {filteredBeneficiaries.map((beneficiary) => (
                <div key={beneficiary.id} className="flex items-center justify-between p-4 border rounded-lg">
                  <div className="flex items-center gap-4">
                    <Avatar className="h-12 w-12">
                      <AvatarImage src={beneficiary.avatar} alt={beneficiary.name} />
                      <AvatarFallback>{beneficiary.name[0]}</AvatarFallback>
                    </Avatar>
                    <div>
                      <div className="flex items-center gap-2">
                        <p className="font-medium">{beneficiary.name}</p>
                        {beneficiary.isFavorite && (
                          <Badge variant="secondary" className="text-xs">‚≠ê Favorite</Badge>
                        )}
                      </div>
                      <p className="text-sm text-muted-foreground">{beneficiary.bankName}</p>
                      <p className="text-xs text-muted-foreground">{beneficiary.accountNumber}</p>
                    </div>
                  </div>
                  <div className="flex gap-2">
                    <Button size="sm" variant="outline">Edit</Button>
                    <Button size="sm">Send</Button>
                  </div>
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      </div>
    </MainLayout>
  );
}
